.button-row-container {
    display: flex;    
    justify-content: center;
    align-items: center;    
}

.home-btn {
    border: 2px solid black;
    border-radius: 5px;   
    margin: 2px;
    padding: 9px 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: smaller;
    cursor: pointer;
    font-family: 'Saira Condensed', sans-serif;
    color: black;
}

.home-btn:hover {
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(255, 255, 255, 0.24);
}

a {
    text-decoration: none;
}